.groupcontainer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  margin: 2vw;
  flex: 1 1 190px;
}

.identity {
  width: max-content;
  font-size: 250%;
  text-decoration: none;
  rotate: 45deg;
  position: relative;
  left: 170px;
  bottom: 25px;
}

.groupName {
  font-family: sans-serif;
  background: linear-gradient(
    to right,
    #ef5350,
    #f48fb1,
    #7e57c2,
    #2196f3,
    #26c6da,
    #43a047,
    #eeff41,
    #f9a825,
    #ff5722
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.panel {
  height: 270px;
  width: 190px;
  position: relative;
}

.panel:hover .card {
  filter: blur(1.5px);
}
.panel:hover .card1 {
  background-size: 215% 135%;
}
.panel:hover .card2 {
  background-size: 115% 115%;
}
.panel:hover .card3 {
  background-size: 155% 115%;
}
.panel:hover .title1 {
  opacity: 0%;
}
.panel:hover .title2 {
  opacity: 0%;
}
.panel:hover .title3 {
  opacity: 0%;
}
.panel:hover .title4 {
  opacity: 0%;
}
.panel:hover .border {
  border: 1px solid rgba(255, 255, 255, 1);
}
.panel:hover .slide {
  bottom: 0px;
}
.panel:hover .ring:before {
  transform: translateX(-50%) translateY(-50%) rotate(310deg);
}
.panel:hover .ring:after {
  transform: translateX(-50%) translateY(-50%) rotate(310deg);
}

.ring,
.card,
.border,
.slide,
.line {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ring {
  color: #fffbf1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  font-size: 170px;
}

.card1 {
  background: #f0ead6;
  position: relative;
  transition: all 1s;
  height: 270px;
  width: 190px;
  border: 1px solid rgba(255, 255, 255, 1);
  background-color: #e63946;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  /* opacity: 50%; */
}

.card2 {
  background: #f0ead6;
  position: relative;
  transition: all 1s;
  height: 270px;
  width: 190px;
  border: 1px solid rgba(255, 255, 255, 1);
  background-color: #ffb703;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.card3 {
  background: #f0ead6;
  position: relative;
  transition: all 1s;
  height: 270px;
  width: 190px;
  border: 1px solid rgba(255, 255, 255, 1);
  background-color: #1d3557;
  background-repeat: no-repeat;
  background-size: 140% 100%;
  background-position: center;
}

.card4 {
  background: #f0ead6;
  position: relative;
  transition: all 1s;
  height: 270px;
  width: 190px;
  border: 1px solid rgba(255, 255, 255, 1);
  background-color: #38b000;
  background-repeat: no-repeat;
  background-size: 140% 100%;
  background-position: center;
}

.ring:before,
.ring:after {
  content: "";
  padding: 0.7em 0.4em;
  position: absolute;
  left: 50%;
  width: 115%;
  top: 50%;
  display: block;
  border: 5px solid #50c9c3;
  border-radius: 50%;
  transition: transform 1s;
  transform: translateX(-50%) translateY(-50%) rotate(50deg);
}

.ring:before {
  border-color: rgb(235, 235, 235) rgb(235, 235, 235) rgba(0, 0, 0, 0)
    rgba(0, 0, 0, 0);
  z-index: -1;
}

.ring:after {
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgb(235, 235, 235)
    rgb(235, 235, 235);
}

p {
  position: relative;
  font-family: sans-serif;
  text-align: right;
  z-index: 10;
}

.title1 {
  font-size: 30px;
  font-weight: 700;
  transition: all 1s;
  top: 0;
  text-align: center;
  color: #1d3557;
}
.title2 {
  font-size: 30px;
  font-weight: 700;
  transition: all 1s;
  top: 0;
  text-align: center;
  color: #fff;
}
.title3 {
  font-size: 30px;
  font-weight: 700;
  transition: all 1s;
  top: 0;
  text-align: center;
  color: #fff;
}
.title4 {
  font-size: 30px;
  font-weight: 700;
  transition: all 1s;
  top: 0;
  text-align: center;
  color: #fff;
}

.para {
  bottom: 0;
  font-size: 16px;
  font-family: "Raleway";
  padding: 20px;
  margin: 0;
  text-align: center;
}

.border {
  position: absolute;
  border: 1px solid rgba(255, 255, 255, 0.5);
  height: 260px;
  width: 180px;
  transition: border 1s;
  overflow: hidden;
}

.slide {
  height: 260px;
  width: 180px;
  position: absolute;
  border: 1px solid black;
  bottom: -270px;
  background: rgba(0, 0, 0, 0.5);
  transition: bottom 1s;
  flex-direction: column;
}

.fa-plane {
  font-size: 16px;
}
